<script>
export default {
  data() {
    return {
      box: false,
      role: false,
      candidates: false,
      archive: false,
      settings: false,
      active: "var(--main-text",
      disabled: "var(--second-text)"
    }
  },
  computed: {
    boxColor() {
      return this.box ? this.active : this.disabled;
    },
    roleColor() {
      return this.role ? this.active : this.disabled;
    },
    candidatesColor() {
      return this.candidates ? this.active : this.disabled;
    },
    archiveColor() {
      return this.archive ? this.active : this.disabled;
    },
    settingsColor() {
      return this.settings ? this.active : this.disabled;
    },

  },
  methods:  {
    rout(r) {
        this.$router.push(r);
        console.log(window.location.pathname)
        if (r == "/box") {
          this.box = true
          this.role = false
          this.candidates = false
          this.archive = false
          this.settings = false
          } else 
        if (r == "/role") {
          this.role = true
          this.box = false
          this.candidates = false
          this.archive = false
          this.settings = false
        } else 
        if (r == "/candidates") {
          this.candidates = true
          this.box = false
          this.role = false
          this.archive = false
          this.settings = false
        } else 
        if (r == "/archive") {
          this.archive = true
          this.box = false
          this.role = false
          this.candidates = false
          this.settings = false
        } else 
        if (r == "/settings") {
          this.settings = true
          this.box = false
          this.role = false
          this.candidates = false
          this.archive = false
        } else {
          this.box = false
          this.role = false
          this.candidates = false
          this.archive = false
          this.settings = false
        }
    },
  }
}
</script>
